












































import Vue from 'vue';
import i18n from '@/locale';
import { Transaction } from '@/interfaces/explorer';

export default Vue.extend({
  name: 'ContractTxList',
  data: () => ({
    isLoaded: false,
    itemsPerPage: 10,

    headers: [
      {
        text: i18n.t('TransactionHash').toString(),
        align: 'start',
        sortable: false,
        value: 'hash',
      },
      { text: i18n.t('TransactionFrom').toString(), value: 'from', sortable: false },
      { text: i18n.t('TransactionTo'), value: 'to', sortable: false },
      { text: i18n.t('TransactionBlockNumber'), value: 'blockNumber', sortable: true },
      { text: i18n.t('TransactionSignature'), value: 'signature', sortable: false },
    ],
    transactions: [] as Array<Transaction>,
  }),
  methods: {
    async getTransactions(skip: number, limit: number) {
      this.transactions = await this.$store.dispatch('explorer/getTransactions', {
        where: {
          to: this.$route.params['hash'].toLowerCase(),
        },
        sort: {
          blockNumber: -1,
          transactionIndex: -1,
        },
        limit,
        skip,
      });
      this.isLoaded = true;
    },
    setItemPerPage(count: number) {
      this.itemsPerPage = count;
      this.getTransactions(0, count);
    },
    setPage(index: number) {
      this.getTransactions(this.itemsPerPage * (index - 1), this.itemsPerPage);
    },
    async redirect(item: any) {
      await this.$router.push(`/explorer/tx/${item.hash}`);
    },
  },
  created() {
    this.getTransactions(0, this.itemsPerPage);
  },
});
